import mySelect from '@/components/my-select/xfl-select.vue';
import switchPatient from '@/components/switch-patient/switch-patient.vue';
export default {
	components: {
		switchPatient,
		mySelect
	},
	data() {
		return {
			cardinfo: [
				{
					name: '张三',
					cardid: '1548588'
				},
				{
					name: '李四',
					cardid: '00000'
				}
			],
			isMyWait: true,
			mouthList: ['三个月', '四个月'],
			typeList: ['报告类型(不限)', '检查报告', '检验报告'],
			jiaobiao: [
				'#40de40',
				'#ff0101'
			],
			jiaobiaoText: [
				'检查',
				'检验'
			],
			inspectionList:[
				{
					type: 0,
					status: 0,
					time: '2019-10-14 11:31:17',
					project: '血常规+CRP'
				},
				{
					type: 1,
					status: 1,
					time: '2019-10-14 11:31:17',
					project: '血常规+CRP'
				}
			]
		}
	},
	methods:{
		gotoInformation: function (type){
			let url = ''
			if(type == 0){
				url = '../../inspection-test/inspection-test'
			}else if(type == 1){
				url = '../../inspection-information/inspection-information'	
			}
			uni.navigateTo({
				url: url
			})
		}
	}
};
